import * as React from 'react';
import * as jangbeeConfig from '../../../jbcallconfig.json';

import { DefaultNavigationProps, Work } from 'src/types';
import { abandonWork, acceptWorkRequest, applyFirmWork, applyWork } from 'src/container/firmwork/actions';

import { Alert } from 'react-native';
import createCtx from 'src/contexts/CreateCtx';
import { noticeUserError } from 'src/container/request';
import url from 'src/constants/Url';
import useAxios from 'axios-hooks';
import { useLoginContext } from 'src/contexts/LoginContext';

interface Context {
  navigation: DefaultNavigationProps;
  openWorkList: Array<object>;
  matchedWorkList: Array<object>;
  refreshing: boolean;
  matchedRefreshing: boolean;
  tabIndex: number;
  applyWork: (workId: string) => void;
  applyFirmWork: (workId: string) => void;
  acceptWork: (work: object) => void;
  abandonWork: (workId: string) => void;
  refetchOpenWorkList: () => void;
  refetchMatchedWorkList: () => void;
  setTabIndex: (i: number) => void;
}

const [useCtx, Provider] = createCtx<Context>();

interface Props {
  children?: React.ReactElement;
  navigation: DefaultNavigationProps;
}

const FirmWorkProvider = (props: Props): React.ReactElement =>
{
  const { user, firm, userProfile, openWorkPaymentModal, openCouponModal } = useLoginContext();
  const [refreshing, setRefreshing] = React.useState(false);
  const [matchedRefreshing, setMatchedRefreshing] = React.useState(false);
  const [tabIndex, setTabIndex] = React.useState(0);

  // Server Data State
  const [openWorkListResponse, openWorkListRequest] = useAxios(`${url.JBSERVER_WORK_FIRM_OPEN}?equipment=${firm?.equiListStr}` +
    `&accountId=${user?.uid}`);
  const [matchedWorkListResponse, matchedWorkListRequest] = useAxios(`${url.JBSERVER_WORK_FIRM_MATCHED}?` +
    `equipment=${firm?.equiListStr}&accountId=${user?.uid}`);

  // Didmount/Unmount
  React.useEffect(() =>
  {
    if (!firm?.equiListStr)
    {
      Alert.alert(
        '   ',
        '  ?     ',
        [
          { text: ' ' },
          {
            text: ' ',
            onPress: (): void => props.navigation.navigate('FirmMyInfo')
          }
        ],
        { cancelable: false }
      );
    }

    const refresh = props.navigation.getParam('refresh', undefined);
    if (refresh) { openWorkListRequest(); matchedWorkListRequest() }
  }, [firm, props.navigation.state]);

  // Error Handling Of Server API Call
  if (openWorkListResponse.error)
  {
    noticeUserError('Firm Work Provider(getFirmOpenWorkList)', openWorkListResponse.error.message, user);
  }
  if (matchedWorkListResponse.error)
  {
    noticeUserError('FirmWorkProvider(getFirmMatchedWorkList)', matchedWorkListResponse.error.message, user);
  }

  // State Action
  const successApply = (work: Work): void =>
  {
    //   
    const clientAccountId = work.accountId;

    openWorkListRequest();
    matchedWorkListRequest();
    setTabIndex(1);
  };

  const failApply = (): void =>
  {
    openWorkListRequest();
  };

  const successAcceptWork = (): void =>
  {
    // @TODO  

    openWorkListRequest();
    matchedWorkListRequest();
    setTabIndex(1);
  };

  const failAcceptWork = (): void =>
  {
    Alert.alert(
      ' ',
      ' ,  ,  3      '
    );

    openWorkListRequest();
  };

  // Consumer States
  const states = {
    navigation: props.navigation, tabIndex,
    openWorkList: openWorkListResponse && openWorkListResponse.data ? openWorkListResponse.data : [],
    refreshing, matchedRefreshing,
    matchedWorkList: matchedWorkListResponse && matchedWorkListResponse.data ? matchedWorkListResponse.data : []
  };

  // Consumer Actions
  const actions = {
    setTabIndex,
    setRefreshing,
    applyWork: (workId: string): void =>
    {
      if (userProfile.sid)
      {
        Alert.alert(
          '    ?',
          ' ,    \n',
          [
            { text: '' },
            { text: '', onPress: (): void => applyWork(workId, user, openWorkListRequest) }
          ]
        );
      }
      else
      {
        Alert.alert(
          '    ?',
          ' ,    \n' +
          `       ( ${jangbeeConfig.workMatchingFee})`,
          [
            { text: '' },
            { text: '', onPress: (): void => applyWork(workId, user, openWorkListRequest) },
            { text: '', onPress: (): void => openWorkPaymentModal(jangbeeConfig.workMatchingFee, applyWork, [workId, user, openWorkListRequest]) }
          ]
        );
      }
    },
    applyFirmWork: (work: Work): void =>
    {
      console.log('>>> applyfirmwork: ', work);
      // '   ,        .',
      if (firm && work.modelYearLimit && firm.modelYear && work.modelYearLimit <= firm.modelYear)
      {
        Alert.alert(',    ', `${work.modelYearLimit}     (: ${firm.modelYear})`);

        return;
      }
      console.log('>>> userProfile', userProfile);
      //  SID  
      if (userProfile.sid)
      {
        Alert.alert(
          '',
          `    ?\n\n ${jangbeeConfig.workMatchingFee} .`,
          [
            { text: '' },
            {
              text: '', onPress: (): void =>
              {
                applyFirmWork(user.uid, work, userProfile.sid, false)
                  .then((res) => res ? successApply() : failApply())
                  .catch((error) => noticeUserError('FirmWorkAction[Apply]', `${error.name},${error.message}`, user));
              }
            }
          ]
        );
      }
      // SID     
      else
      {
        Alert.alert(
          '  ',
          '     [100]\n      (   )',
          [
            { text: '' },
            { text: '', onPress: (): void => openWorkPaymentModal(jangbeeConfig.workMatchingFee, applyFirmWork, [user.uid, work, userProfile.sid, false]) }
          ]
        );
      }
    },
    acceptWork: (work: object): void =>
    {
      console.log('>>> work:', work);
      if (!userProfile || !userProfile.sid)
      {
        Alert.alert(
          '  ( 2)',
          '\n  (,  )',
          [
            { text: '', onPress: (): void => this.abandonWork(work.id) },
            {
              text: ' ',
              onPress: (): void => { openWorkPaymentModal(jangbeeConfig.workMatchingFee) }
            }
          ],
          { cancelable: true }
        );
        return;
      }
      Alert.alert(
        '  ,   .',
        ' ,  (  ) ! []   .',
        [
          { text: '', onPress: (): void => abandonWork(user, work.id, openWorkListRequest) },
          // {
          //   text: '',
          //   onPress: (): void => { openCouponModal() }
          // },
          {
            text: '',
            onPress: (): void =>
            {
              acceptWorkRequest(work.id, user, false, userProfile.sid)
                .then((res) => res ? successAcceptWork() : failAcceptWork())
                .catch((error) => noticeUserError('FimWorkAction(acceptWork api call error)', error.message, user));
            }
          }
        ]
      );
    },
    abandonWork: (workId: string): void =>
    {
      Alert.alert(
        '',
        '  ?',
        [
          { text: '', onPress: (): void => abandonWork(user, workId, openWorkListRequest) },
          {
            text: ''
          }
        ]
      );
    },
    refetchOpenWorkList: (): void => { openWorkListRequest() },
    refetchMatchedWorkList: (): void => { matchedWorkListRequest() }
  };

  // UI Component
  return (
    <Provider value={{ ...states, ...actions }}>{props.children}</Provider>
  );
};

export { useCtx as useFirmWorkProvider, FirmWorkProvider };
