import * as api from 'api/api';

import { CallHistory, CallHistoryType } from "./type";

import { Share } from "react-native";
import { formatNumber } from "src/utils/NumberUtils";
import { formatTelnumber } from "src/utils/StringUtils";

export const deleteFirmHarmCase = (id: string): Promise<object> => {
  return api
    .deleteCliEvalu(id)
}

export const filterCallHistory = (callHistory: Array<CallHistory>):Array<CallHistory> => {
  const callNumberArr = [];
  const filterdHistory = callHistory.filter((history) => {
    if (callNumberArr.includes(history.phoneNumber)) { return false }
    callNumberArr.push(history.phoneNumber);

    return history.rawType !== CallHistoryType.OUTGOING;
  });

  return filterdHistory;
};

export const shareNotExistCEvalu = (filterStr, filterTime) => {
  let searWord = filterStr;

  try {
    Share.share({
      message: `[${searWord}]  \n\n: ${searWord}\n:   \n : ${filterTime}\n\n    [ ]   .\nhttps://play.google.com/store/apps/details?id=com.kan.jangbeecall`
    });
  } catch (error) {
    console.error(error);
  }
}

export const shareClientEvalu = (evalu, searchTime) => {
  try {
    Share.share({
      message: `[${formatTelnumber(
        evalu.telNumber
      )}] \n\n${makeShareContent(
        '',
        evalu.cliName
      )}${makeShareContent('', evalu.firmName)}${makeShareContent(
        '2',
        evalu.telNumber2
      )}${makeShareContent('3', evalu.telNumber3)}${makeShareContent(
        '',
        evalu.firmNumber
      )}${makeShareContent('', evalu.equipment)}${makeShareContent(
        '',
        evalu.local
      )}${makeSharePriceContent(
        '',
        formatNumber(evalu.amount)
      )}${makeShareContent(
        '', `\n${evalu.reason}`
      )}\n : ${searchTime}\n\n    [ ]   .\nhttps://play.google.com/store/apps/details?id=com.kan.jangbeecall`
    });
  } catch (error) {
    console.error(error);
  }
}

