import React from 'react';
import { Alert, Modal } from 'react-native';
import styled from 'styled-components/native';
import { AirbnbRating } from 'react-native-elements';
import * as api from 'api/api';
import fonts from 'constants/Fonts';
import JBIcon from 'atoms/JBIcon';
import JBButton from 'molecules/JBButton';
import JBTextInput from 'molecules/JBTextInput';
import JBErrorMessage from 'organisms/JBErrorMessage';
import { validate } from 'utils/Validation';
import { notifyError } from 'common/ErrorNotice';

const Container = styled.View`
  flex: 1;
  background-color: rgba(0, 0, 0, 0.5);
  ${props =>
    props.size === 'full' &&
    `
    background-color: white;
  `}
`;

const ContentsView = styled.View`
  flex: 1;
  background-color: white;
  padding: 20px;
  ${props =>
    props.size === 'full' &&
    `
  `}
`;

const TopWrap = styled.View`
  flex-direction: row;
  align-items: center;
  margin-bottom: 25px;
`;

const TitleWrap = styled.View`
  flex: 1;
  align-items: center;
`;

const Title = styled.Text`
  font-family: ${fonts.titleTop};
  font-size: 18px;
  font-weight: bold;
`;

const RatingWrap = styled.View`
  margin-bottom: 20px;
  align-items: center;
`;

const RatingText = styled.Text`
  font-family: ${fonts.title};
  font-size: 14px;
`;

const CommentWrap = styled.View`
  height: 200px;
`;

export default class ClientEstimateFirmModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  /**
   *    
   */
  completeAction = () => {
    const { completeAction, closeModal } = this.props;

    const newData = this.validateForm();

    api
      .createFirmEvalu(newData)
      .then(result => {
        if (result) {
          completeAction();
          closeModal();
          return;
        }

        notifyError(
          ' ',
          `   ,   (: ${result})`
        );
      })
      .catch(error => notifyError(error.name, error.message));
  };

  /**
   *   
   */
  validateForm = () => {
    const { comment, rating } = this.state;
    const { workId } = this.props;

    // Validation Error Massage Initialize
    this.setState({
      commentValErrMessage: ''
    });

    if (!workId) {
      Alert.alert(
        '    ',
        '       '
      );
    }

    const v = validate('textMax', comment, true, 200);
    if (!v[0]) {
      this.setState({ commentValErrMessage: v[1] });
      return false;
    }

    const ratingData = {
      workId,
      rating,
      comment
    };

    return ratingData;
  };

  render() {
    const { isVisibleModal, closeModal } = this.props;
    const { comment, commentValErrMessage } = this.state;

    return (
      <Modal
        animationType="slide"
        transparent
        visible={isVisibleModal}
        onRequestClose={() => closeModal()}
      >
        <Container>
          <ContentsView>
            <TopWrap>
              <JBIcon name="close" size={33} onPress={() => closeModal()} />
              <TitleWrap>
                <Title> ?</Title>
              </TitleWrap>
            </TopWrap>
            <RatingWrap>
              <RatingText></RatingText>
              <AirbnbRating
                count={5}
                reviews={[
                  '',
                  '',
                  '',
                  ',    ',
                  ', '
                ]}
                defaultRating={0}
                onFinishRating={rating => this.setState({ rating })}
                size={35}
              />
            </RatingWrap>

            <CommentWrap>
              <JBTextInput
                title=" :"
                value={comment}
                onChangeText={text => this.setState({ comment: text })}
                placeholder=" "
                multiline
                numberOfLines={5}
              />
              <JBErrorMessage errorMSG={commentValErrMessage} />
            </CommentWrap>
            <JBButton
              title=""
              onPress={() => this.completeAction()}
              size="full"
              Primary
            />
          </ContentsView>
        </Container>
      </Modal>
    );
  }
}
